import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { ValidatorForm, TextValidator } from "react-material-ui-form-validator";
import {Picker} from 'emoji-mart'
import 'emoji-mart/css/emoji-mart.css'

class PaletteMetaForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      stage:"form",
      newPaletteName: ""
    };
    this.handleChange = this.handleChange.bind(this);
    this.showemojipicker=this.showemojipicker.bind(this);
    this.savePallete=this.savePallete.bind(this)
  }
  componentDidMount() {
    ValidatorForm.addValidationRule("isPaletteNameUnique", value =>
      this.props.palettes.every(
        ({ paletteName }) => paletteName.toLowerCase() !== value.toLowerCase()
      )
    );
  }
  handleChange(evt) {
    this.setState({
      [evt.target.name]: evt.target.value
    });
  }
  handleClickOpen = () => {
    this.setState({ open: true });
  };

  handleClose = () => {
    this.setState({ open: false });
  };
  showemojipicker(){
      this.setState({stage: "emoji"})
  }
  savePallete(emoji){
    const newPalette = {paletteName: this.state.newPaletteName, emoji: emoji.native}
    this.props.handleSubmit(newPalette)
 
  } 

  render() {
    const { newPaletteName } = this.state;
    const {hideForm, handleSubmit} =this.props
    return (
        <div>
        <Dialog
            open={this.state.stage ==='emoji'}
            onClose={hideForm}
        >
        <DialogTitle id='form-dialog-title'>Eric's Picker (Emoji)</DialogTitle>
        <Picker
            onSelect={this.savePallete}
            title="Choose your Emoji"
        />
        </Dialog>
        <Dialog
          open={this.state.stage ==='form'}
          onClose={this.handleClose}
          aria-labelledby='form-dialog-title'
          onClose={hideForm}
        >
          <DialogTitle id='form-dialog-title'>Make a Palette Name</DialogTitle>
          <ValidatorForm
              onSubmit={this.showemojipicker}
            >
          <DialogContent>
            <DialogContentText>
              Enter your Palette name. you are not able to make same name of Palette. make sure it is unique
            </DialogContentText>
              <TextValidator
                label='Palette Name'
                value={newPaletteName}
                name='newPaletteName'
                fullWidth
                margin = 'normal'
                onChange={this.handleChange}
                validators={["required", "isPaletteNameUnique"]}
                errorMessages={["Enter Palette Name", "Name already used"]}
              />
                
            
          </DialogContent>
          <DialogActions>
            <Button onClick={hideForm} color='primary'>
              Cancel
            </Button>
            <Button variant='contained' color='primary' type='submit'>
                Save Palette
              </Button> 
          </DialogActions>
          </ValidatorForm>
        </Dialog>
        </div>
    );
  }
}
export default PaletteMetaForm;